import { mapState, mapActions } from 'vuex';

export default {
  name: 'SubtitlesMenuItem',
  computed: {
    ...mapState('mediaPlayer/captions', {
      selected: 'subtitles',
    }),
  },
  methods: {
    ...mapActions('mediaPlayer/captions', ['toggleSubtitles']),
    /**
     * @public
     */
    focus() {
      this.$nextTick(() => this.$refs.kCheckbox.focus());
    },
  },
  $trs: {
    subtitles: 'Subtitles',
  },
};

